import React from "react";
import classNames from "classnames";

import "./itemArticle.scss";

const ItemArticle = ({ className, title, imageSrc, imageAlt, url, dateTime }) => {
	const itemArticleClassnames = classNames({
		[className]: className,
		"item-article": true,
	});

	const hours = new Date(dateTime).getHours();
	const minutes = new Date(dateTime).getMinutes();
	const time = `${hours > 9 ? hours : `0${hours}`}:${minutes > 9 ? minutes : `0${minutes}`}`;

	return (
		<li className={itemArticleClassnames}>
			<a className="item-article__image" href={url} aria-label="   ">
				<img src={imageSrc} alt={imageAlt} />
			</a>

			<a href={url} className="item-article__title" aria-label="   ">
				<time className="item-article__time" dateTime={dateTime}>
					{time}
				</time>
				{title}
			</a>
		</li>
	);
};

export default ItemArticle;
