import React from "react";
import classNames from "classnames";

import { getFormattedTime } from "../../services";

import "./itemVideo.scss";

const ItemVideo = ({ className, title, dateTime, url, imageSrc, alt, isLarge }) => {
	const itemClassnames = classNames({
		[className]: className,
		"item-video": true,
		"item-video--large": isLarge,
	});

	return (
		<li className={itemClassnames}>
			<a className="item-video__link" href={url}>
				<img className="item-video__preview" src={imageSrc} alt={alt} />
			</a>
			<time dateTime={dateTime} className="item-video__time">
				{getFormattedTime(dateTime)}
			</time>
			<a className="item-video__title" href={url}>
				{title}
			</a>
		</li>
	);
};

export default ItemVideo;
